// import ../_variables.sass
/* --- Background Colors --- */
// Primary
.bg-primary {
  background: $normal-primary;
  &-light {
    background: $light-primary; }
  &-dark {
    background: $dark-primary; }
  &-hover:hover {
    @extend .bg-primary; } }

// Secondary
