/* --- Background gradients colors --- */
// Most used in web design
.primary-gradient {
  background: linear-gradient(90deg, #45cafc, #303f9f); }
.info-gradient {
  background: linear-gradient(90deg, #2096ff, #05ffa3); }
.warning-gradient {
  background: linear-gradient(90deg, #ffd86f, #fc6262); }
.navy-gradient {
  background: linear-gradient(90deg, #30cfd0, #330867); }
.purple-gradient {
  background: linear-gradient(90deg, #ff6ec4, #7873f5); }
.pink-gradient {
  background: linear-gradient(90deg, #fbc2eb, #a6c1ee); }
