// import ../_variables.sass
/* --- --- Typography --- --- */

@import url('https://fonts.googleapis.com/css?family=Roboto:100,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Courier+Prime:100,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Raleway:100,400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Muli:100,400,700&display=swap');

/* Font-size */
.fz-05rem {
  font-size: .5rem; }
.fz-1rem {
  font-size: 1rem; }
.fz-2rem {
  font-size: 2rem; }
.fz-3rem {
  font-size: 3rem; }
.fz-4rem {
  font-size: 4rem; }
.fz-5rem {
  font-size: 5rem; }

/* Links */
.td-none {
  text-decoration: none !important; }

/* Fonts */
.montserrat {
  font-family: $montserrat; }
.roboto {
  font-family: $roboto; }
.courier {
  font-family: $courier-prime; }
.open-sans {
  font-family: $open-sans; }
.raleway {
  font-family: $raleway; }
.muli {
  font-family: $muli; }

/* Text Colors */
.text-navy {
  color: $normal-navy;
  &-light {
    color: $light-navy;
    &-hover:hover {
      color: $light-navy; } }
  &-dark {
    color: $dark-navy;
    &-hover:hover {
      color: $dark-navy; } }
  &-hover:hover {
    @extend .text-navy; } }
