/* Font Weight */
.text-bold {
  font-weight: 700;
  &-hover:hover {
    @extend .text-bold; } }
.text-normal {
  font-weight: 400;
  &-hover:hover {
    @extend .text-normal; } }
.text-thin {
  font-weight: 100;
  &-hover:hover {
    @extend .text-thin; } }
.text-underline {
  text-decoration: underline;
  &-hover:hover {
    @extend .text-underline; } }

/* Letter Spacing */
.ls-1 {
  letter-spacing: 1px;
  &-hover:hover {
    @extend .ls-1; } }
.ls-2 {
  letter-spacing: 2px;
  &-hover:hover {
    @extend .ls-2; } }
.ls-3 {
  letter-spacing: 3px;
  &-hover:hover {
    @extend .ls-3; } }
.ls-4 {
