  &-hover:hover {
    @extend .ls-4; } }
.ls-5 {
  letter-spacing: 5px;
  &-hover:hover {
    @extend .ls-5; } }

/* Line Height */
.lh-05 {
  line-height: $spacer-50;
  &-hover:hover {
    @extend .lh-05; } }
.lh-06 {
  line-height: $spacer-60;
  &-hover:hover {
    @extend .lh-06; } }
.lh-07 {
