/* Buttons Styles */
.btn {
  transition: .5s ease all;
  &:hover {
    opacity: .8 !important; }
  &.btn-rounded-40 {
    border-radius: 40px;
    &-important {
      border-radius: 40px !important; } }
  &.btn-circle {
    border-radius: $spacer-50;
    &-important {
      border-radius: $spacer-50 !important; } }
  &.btn-outline {
    @extend .text-bold;
    border: 2px solid !important; } }

// Bootstrap 4
// Primary
.btn-primary {
  &.btn-bg-light {
    background: $light-primary;
    border-color: $light-primary; }
  &.btn-bg-dark {
    background: $dark-primary;
    border-color: $dark-primary; }
  &.btn-outline {
    background: $normal-white;
    border-color: $normal-primary;
    color: $normal-primary;
    &:hover {
      background-color: $light-primary !important;
      color: $normal-white !important; } } }

